<template>
    <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            
            
            <el-form-item label="" prop="name">
                <el-input v-model="ruleForm.name" style="width: 50%;" placeholder=""></el-input>
            </el-form-item>
            
            
            <el-form-item label="" prop="address">
                <el-select v-model="ruleForm.address" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
                
            </el-form-item>

            <el-form-item label="" prop="particularAddress">
                <el-input v-model="ruleForm.particularAddress" style="width: 50%;display: -webkit-box;" placeholder=""></el-input>
            </el-form-item>
            
            <el-form-item
            style="width: 70%;"
                prop="email"
                label=""
                
            >
                <el-input v-model="ruleForm.email" placeholder=""></el-input>
            </el-form-item>

            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="password">
                    <el-input type="password" v-model="ruleForm.password" placeholder="" style="width: 30%;" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="checkPass">
                    <el-input type="password" v-model="ruleForm.checkPass" placeholder="" style="width: 30%;" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>

            <el-form-item label="" prop="beVolunteer">
                <el-switch v-model="ruleForm.beVolunteer"></el-switch>
            </el-form-item>
            
            
            <el-form-item label="" prop="organizationType">
                <el-radio-group v-model="ruleForm.organizationType">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            
            
            <el-form-item label="" prop="other">
                <el-input type="textarea" v-model="ruleForm.other"></el-input>
            </el-form-item>
            
            
            <el-form-item>
                <el-button type="primary" @click="OrgRegister"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'OrganizationRegister',

    data() {
         var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.ruleForm.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                email:'',
                name: '',
                address: '',
                particularAddress:'',
                organizationType: '',
                password: '',
                other: '',
                beVolunteer: 'false',
                checkPass:''
            },
            
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15 ', trigger: 'blur' }
                ],
                address: [
                    { required: true, message: '', trigger: 'change' }
                ],
                
                other: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ],
                email:[
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: ['blur', 'change'] }
                ]
            
            },
        
        }
        
    },

    mounted() {
        console.log(this)
    },

    methods: {
         
         OrgRegister() {
            var params = new URLSearchParams();
            console.log(this)
            params.append('email', this._data.ruleForm.email)
            params.append('orgName', this._data.ruleForm.name)
            params.append('address', this._data.ruleForm.address)
            params.append('particularAddress', this._data.ruleForm.particularAddress)
            params.append('organizationType', this._data.ruleForm.organizationType)
            params.append('orgPassword', this._data.ruleForm.password)
            params.append('orgOther', this._data.ruleForm.other)
            params.append('beVolunteer', this._data.ruleForm.beVolunteer)
            
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/OrgRegister",
                params
            }).then(res => {
                 this.ruleForm = {
                    email: '',
                     name: '',
                     address: '',
                     particularAddress: '',
                     organizationType: '',
                     password: '',
                     other: '',
                     beVolunteer: 'false',
                     checkPass: ''
                }
                this.$message({
                    type: "success",
                    message: "",
                });

                console.log(res)
            })

        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    
    },
};
</script>

<style lang="css" scoped>

</style>