<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" style="width: 60%;" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>

        <el-form-item label="" prop="taskType">
            <el-radio-group v-model="ruleForm.taskType">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label="" prop="address">
            <el-select v-model="ruleForm.address" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
            </el-select>
        
        </el-form-item>
        
        <el-form-item label="" prop="particularAddress">
            <el-input v-model="ruleForm.particularAddress" style="width: 50%;display: -webkit-box;" placeholder=""></el-input>
        </el-form-item>


        <el-form-item label="" style="width: 70%;" required>
            <el-col :span="11">
                <el-form-item prop="startTime">
                    <el-date-picker 
                    type="date" 
                    value-format="yyyy-MM-dd"
                    placeholder="" 
                    v-model="ruleForm.startTime" 
                    format="yyyy  MM  dd "
                    style="width: 100%;">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col class="line" :span="2"></el-col>
            <el-col :span="11">
                <el-form-item prop="endTime">
                    <el-date-picker 
                    type="date" 
                    v-model="ruleForm.endTime" 
                    placeholder="" 
                    format="yyyy  MM  dd "
                    value-format="yyyy-MM-dd" 
                    style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-col>
        </el-form-item>


        <el-form-item label="" prop="skill">
            <el-checkbox-group v-model="ruleForm.skill">
                <el-checkbox label="" name="skill"></el-checkbox>
                <el-checkbox label="" name="skill"></el-checkbox>
                <el-checkbox label="" name="skill"></el-checkbox>
                <el-checkbox label="" name="skill"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>

        <el-form-item label="" prop="educationLevel">
            <el-radio-group v-model="ruleForm.educationLevel">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label="" prop="personMaxNum">
            <el-input-number v-model="ruleForm.personMaxNum" :min="1" :max="1000" label=""></el-input-number>
        </el-form-item>
        
        <el-form-item label="" prop="other">
            <el-input type="textarea" v-model="ruleForm.other"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="Issue('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'IssueDemand',

    data() {
        return {
             ruleForm: {
                name: '',
                taskType:'',
                address:'',
                particularAddress:'',
                startTime:'',
                endTime:'',
                skill:[],
                educationLevel:'',
                personMaxNum:0,
                other:'', 
                email:''   
            },

            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                address: [
                    { required: true, message: '', trigger: 'change' }
                ],
                startTime: [
                    { required: true, message: '', trigger: 'change' }
                ],
                endTime: [
                    {  required: true, message: '', trigger: 'change' }
                ],
                taskType: [
                    {  required: true, message: '', trigger: 'change' }
                ],
                particularAddress:[
                    { required: true, message: '', trigger: 'blur'}
                ],
                other: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                
            }
        };
    },

    mounted() {
        this.ruleForm.email = this.$route.query.email
    },

    methods: {
        
        
        Issue() {
            var params = new URLSearchParams();
            params.append('taskName', this._data.ruleForm.name)
            params.append('taskType', this._data.ruleForm.taskType)
            params.append('taskAddress', this._data.ruleForm.address)
            params.append('particularAddress', this._data.ruleForm.particularAddress)
            params.append('startTime', this._data.ruleForm.startTime)
            params.append('endTime', this._data.ruleForm.endTime)
            params.append('skill', this._data.ruleForm.skill)
            params.append('educationLevel', this._data.ruleForm.educationLevel)
            params.append('personMaxnum', this._data.ruleForm.personMaxNum)
            params.append('other', this._data.ruleForm.other)
            params.append('orgEmail', this._data.ruleForm.email)
            console.log(this._data.ruleForm.startTime)
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/IssueTask",
                params
            }).then(res => {
                this.ruleForm = {
                    name: '',
                    taskType: '',
                    address: '',
                    particularAddress: '',
                    startTime: '',
                    endTime: '',
                    skill: [],
                    educationLevel: '',
                    personMaxNum: 0,
                    other: '',
                    email: '' 
                }
                this.$message({
                    type: "success",
                    message: "",
                });
                console.log(res)
            })

        },

        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
    },
};
</script>

<style lang="css" scoped>

</style>