<template>
    <div>
        
        <div class="userfrom">
            <el-form ref="form" :model="form" label-width="80px">

                <el-form-item>
                    <el-upload class="avatar-uploader" 
                        name="file"
                        action="http://39.107.32.92:80/api/imgPost" 
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <div slot="tip" class="el-upload__tip" style="text-align: center;"></div>
                        <img v-if="form.imageUrl" :src="form.imageUrl" class="avatar">
                    
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    
                    </el-upload>
                </el-form-item>

                
                <el-form-item label="">
                    <el-input 
                    v-model="form.name" 
                    style="width: 25%;"
                    placeholder=""></el-input>
                </el-form-item>


                <el-form-item label="">
                    <el-select v-model="form.certificateType" placeholder="" style="width: 25%;">
                        <el-option label="" value="IdCard"></el-option>
                        <el-option label="" value="veterenCard"></el-option>
                    </el-select>
                </el-form-item>


                <el-form-item label="">
                    
                        <el-input v-model="form.certificateNum" placeholder="" style="width: 40%;"></el-input>
                    
                </el-form-item>


                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="form.birthday" style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>

                <el-form-item label="">
                    <el-radio-group v-model="form.sex" size="medium">
                        <el-radio label="">
                            <i class="el-icon-male"></i>
                        </el-radio>
                
                        <el-radio label="">
                            <i class="el-icon-female"></i>
                        </el-radio>
                
                    </el-radio-group>
                </el-form-item>
                
                
                <el-form-item label="">
                    <el-select v-model="form.educationalLevel" placeholder="" style="width: 25%;">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        
                    </el-select>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="form.phoneNum" style="width: 35%;" placeholder=""></el-input>
                </el-form-item>
                
                <el-form-item label="">
                    <el-input placeholder="" v-model="form.passWord" style="width: 20%;" show-password></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input placeholder="" v-model="form.isPassword" style="width: 20%;" show-password></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="form.presentAddress" placeholder="" style="width: 40%;"></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-switch v-model="form.voluntarily"></el-switch>
                </el-form-item>

                <el-form-item label="" prop="skill">
                    <el-checkbox-group v-model="form.skill">
                        <el-checkbox label=""></el-checkbox>
                        <el-checkbox label=""></el-checkbox>
                        <el-checkbox label=""></el-checkbox>
                        <el-checkbox label=""></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="">
                    <el-input type="textarea" v-model="form.other"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="registered"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
export default {
    name: 'UserRegister',

    data() {
        return {
             form: {
                name: '',
                certificateType: '',
                certificateNum:'',
                birthday:'',
                sex:'',
                educationalLevel:'',
                phoneNum:'',
                passWord:'',
                skill: [],
                imageUrl: '',
                other:'',
                presentAddress:'',
                isPassword:'',
                voluntarily:false              
            },
            picUrl:''
        };
    },

    mounted() {
        
    },

    methods: {
        
        registered(){
            this.$options.methods.registere(this);
            this.$options.methods.conPhoneAndpic(this);

        },
        conPhoneAndpic(that){
            var params = new URLSearchParams();
            params.append('phoneNum', that._data.form.phoneNum)
            params.append('picUrl', that._data.picUrl)
            that.$axios({
                method: "post",
                url: "http://39.107.32.92:80/api/conPhoneNumAndUrl",
                crossDomain: true,
                params
            }).then(res => {
                console.log(res)
            })
        },
        registere(that) {
            var params = new URLSearchParams();
            params.append('volName',that._data.form.name)
            params.append('certificateType', that._data.form.certificateType)
            params.append('certificateNum', that._data.form.certificateNum)
            params.append('birthday', that._data.form.birthday)
            params.append('sex', that._data.form.sex)
            params.append('educationalLevel', that._data.form.educationalLevel)
            params.append('phoneNum', that._data.form.phoneNum)
            params.append('volPassword', that._data.form.passWord)
            params.append('skill', that._data.form.skill)
            params.append('other', that._data.form.other)
            params.append('voluntarily', that._data.form.voluntarily)
            params.append('presentAddress', that._data.form.presentAddress) 
            axios({
                method: "post",
                url: "http://39.107.32.92:80/api/VolRegister",
                crossDomain:true,
                params
            }).then(res => {
                that.form = {
                    name: '',
                    certificateType: '',
                    certificateNum: '',
                    birthday: '',
                    sex: '',
                    educationalLevel: '',
                    phoneNum: '',
                    passWord: '',
                    skill: [],
                    imageUrl: '',
                    other: '',
                    presentAddress: '',
                    isPassword: '',
                    voluntarily: false}
                    that.$message({
                        type: "success",
                        message: "",
                    });
                console.log(res)
            })
            
        },
        
        handleAvatarSuccess(res, file) {
            this.form.imageUrl = URL.createObjectURL(file.raw);
            this.picUrl = res
            console.log(res)
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error(' JPG !');
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!');
            }
            return isJPG && isLt2M;
        },
      
    },
};
</script>

<style lang="css" scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .avatar-uploader{
      position: absolute;
      right: 10%;
      
  }
  .el-input__inner{
      width: 30%;
  }
  
</style>